}

.path-input-form {
	display: flex;
	flex-direction: column;
	gap: 15px;
	width: 100%;
	max-width: 500px;
}

.path-input {
	padding: 12px 16px;
	border: 2px solid #ddd;
	border-radius: 8px;
	font-size: 1rem;
	transition: border-color 0.3s;
}

.path-input:focus {
	outline: none;
	border-color: #667eea;
}

.path-input:disabled,
.path-input.disabled {
	background: #f5f5f5;
	cursor: not-allowed;
	color: #999;
}

.info-text {
	color: #666;
	font-size: 0.9rem;
	text-align: center;
	margin-top: 10px;
	padding: 10px;
	background: #f0f8ff;
	border-radius: 6px;
	border: 1px solid #e3f2fd;
}

.scan-btn {
	background: #667eea;
	color: white;
	border: none;
	padding: 12px 24px;
	border-radius: 8px;
	font-size: 1rem;
	cursor: pointer;
