}

.error-message {
	background: #ffebee;
	color: #c62828;
	padding: 15px 20px;
	border-radius: 8px;
	margin-bottom: 20px;
	border-left: 4px solid #c62828;
}

.results-section {
	background: white;
	border-radius: 12px;
	padding: 30px;
	box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
}

.folder-results h2 {
	margin: 0 0 10px 0;
	color: #333;
}

.scan-info {
	color: #666;
	font-size: 0.9rem;
	margin-bottom: 20px;
}

.results-content {
	margin-bottom: 25px;
}

.path-info {
	margin-bottom: 20px;
	padding: 15px;
	background: #f8f9fa;
	border-radius: 8px;
	border-left: 4px solid #667eea;
}

.count-info {
	text-align: center;
	margin: 30px 0;
}

.file-count {
	display: flex;
	flex-direction: column;
	align-items: center;
	gap: 10px;
	margin-bottom: 20px;
}

.download-btn {
	background: #28a745;
	color: white;
	border: none;
	padding: 12px 24px;
	border-radius: 8px;
	font-size: 1rem;
	cursor: pointer;
	transition: background-color 0.3s;
	display: inline-flex;
	align-items: center;
	gap: 8px;
}

.download-btn:hover {
	background: #218838;
}

.download-btn:active {
	transform: translateY(1px);
}

.download-btn:disabled {
	background: #ccc;
	cursor: not-allowed;
	transform: none;
}

.message {
	margin-top: 10px;
	padding: 8px 12px;
	border-radius: 4px;
	font-size: 0.9rem;
	text-align: center;
}

.message.success {
	background: #d4edda;
	color: #155724;
	border: 1px solid #c3e6cb;
}

.message.error {
	background: #f8d7da;
	color: #721c24;
	border: 1px solid #f5c6cb;
}

.count-number {
	font-size: 3rem;
	font-weight: bold;
	color: #4CAF50;
}

.count-label {
	font-size: 1.2rem;
	color: #666;
}

.warning-message {
	margin-top: 8px;
	padding: 8px 12px;
	background-color: #fff3cd;
	border: 1px solid #ffeaa7;
	border-radius: 4px;
	color: #856404;
	font-size: 0.9em;
	font-weight: 500;
}

.progress-container {
	margin-top: 20px;
	padding: 15px;
	background: #f8f9fa;
	border-radius: 8px;
	border: 1px solid #e9ecef;
}

.progress-header {
	display: flex;
	justify-content: space-between;
	align-items: center;
	margin-bottom: 10px;
	font-weight: 500;
	color: #495057;
}

.progress-bar {
	width: 100%;
	height: 8px;
	background: #e9ecef;
	border-radius: 4px;
	overflow: hidden;
	margin-bottom: 10px;
}

.progress-fill {
	height: 100%;
	background: linear-gradient(90deg, #28a745, #20c997);
	border-radius: 4px;
	transition: width 0.3s ease;
}

/* Advanced Progress Bar with Chunks */
.advanced-progress-bar {
	margin: 15px 0;
}

.progress-bar-container {
	display: flex;
	height: 40px;
	border: 2px solid #e9ecef;
	border-radius: 8px;
	overflow: hidden;
	background: #f8f9fa;
}

.progress-chunk {
	position: relative;
	display: flex;
	align-items: center;
	border-right: 2px solid #dee2e6;
	background: #ffffff;
	transition: all 0.3s ease;
}

.progress-chunk:last-child {
	border-right: none;
}

.progress-chunk.completed {
	background: linear-gradient(90deg, #28a745, #20c997);
}

.progress-chunk.current {
	background: linear-gradient(90deg, #ffc107, #fd7e14);
	box-shadow: inset 0 0 10px rgba(255, 193, 7, 0.3);
}

.progress-chunk-fill {
	height: 100%;
	background: rgba(255, 255, 255, 0.8);
	transition: width 0.3s ease;
	border-radius: 0;
}

.progress-chunk.completed .progress-chunk-fill {
	background: rgba(255, 255, 255, 0.2);
}

.progress-chunk.current .progress-chunk-fill {
	background: rgba(255, 255, 255, 0.3);
}

.chunk-file-lines {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding: 0 4px;
}

.file-line {
	width: 1px;
	height: 60%;
	background: rgba(0, 0, 0, 0.2);
	border-radius: 1px;
	transition: background 0.3s ease;
}

.file-line.processed {
	background: rgba(255, 255, 255, 0.8);
}

.progress-chunk.completed .file-line.processed {
	background: rgba(255, 255, 255, 0.9);
}

.progress-chunk.current .file-line.processed {
	background: rgba(255, 255, 255, 0.8);
}

.chunk-label {
	position: absolute;
	top: -25px;
	left: 50%;
	transform: translateX(-50%);
	font-size: 0.75rem;
	font-weight: bold;
	color: #495057;
	background: rgba(255, 255, 255, 0.9);
	padding: 2px 6px;
	border-radius: 4px;
	border: 1px solid #dee2e6;
}

.chunk-info {
	display: flex;
	align-items: center;
	gap: 8px;
}

.chunk-stats {
	font-size: 0.85rem;
	color: #6c757d;
}

.progress-legend {
	display: flex;
	justify-content: center;
	gap: 20px;
	margin-top: 15px;
	padding: 10px;
	background: #f8f9fa;
	border-radius: 6px;
