.chunk-size-input:focus {
	outline: none;
	border-color: #667eea;
}

.chunk-size-input:disabled {
	background: #f5f5f5;
	cursor: not-allowed;
	color: #999;
}

.chunk-help {
	color: #6c757d;
	font-size: 0.9rem;
}

.chunk-recommendation {
	font-size: 0.85rem;
	text-align: center;
	margin-top: 8px;
	display: flex;
	flex-direction: column;
	gap: 4px;
}

.memory-based-rec {
	color: #1976d2;
	font-weight: 600;
	font-style: normal;
}

.file-size-assumption {
	color: #6c757d;
	font-size: 0.75rem;
	font-style: italic;
}

.estimated-files {
	color: #6c757d;
	font-size: 0.8rem;
}

.estimate-item {
	opacity: 0.8;
	border-left: 3px solid #e9ecef;
	padding-left: 8px;
}

.fallback-rec {
	color: #28a745;
	font-weight: 500;
	font-style: italic;
}

.files-list h3 {
	margin: 20px 0 15px 0;
	color: #333;
}

.files-container {
	max-height: 400px;
	overflow-y: auto;
	border: 1px solid #ddd;
	border-radius: 6px;
	padding: 10px;
	background: #fafafa;
}

.files-ul {
	list-style: none;
	padding: 0;
	margin: 0;
}

.file-item {
	padding: 6px 0;
	border-bottom: 1px solid #eee;
	font-family: 'Courier New', monospace;
	font-size: 0.9rem;
}

.file-item:last-child {
	border-bottom: none;
}

.more-files {
	text-align: center;
	color: #666;
	font-style: italic;
	margin-top: 10px;
	padding: 10px;
	background: #fff;
	border-radius: 4px;
	border: 1px dashed #ccc;
}

.reset-btn {
	background: #ff6b6b;
	color: white;
	border: none;
	padding: 12px 24px;
	border-radius: 8px;
	font-size: 1rem;
	cursor: pointer;
	transition: background-color 0.3s;
	width: 100%;
}

.reset-btn:hover {
	background: #ff5252;
}

@media (max-width: 1024px) {
	.two-column-layout {
		grid-template-columns: 1fr;
		gap: 20px;
	}
	
	.app-main {
		max-width: 800px;
	}
}

@media (max-width: 768px) {
	.App {
		padding: 10px;
	}
	
	.app-header h1 {
		font-size: 2rem;
	}
	
	.folder-selector,
	.results-section {
		padding: 20px;
	}
	
	.selector-options {
		gap: 15px;
	}
	
	.path-input-form {
		max-width: 100%;
	}
	
	.two-column-layout {
		grid-template-columns: 1fr;
		gap: 15px;
	}
	
	.welcome-card {
		padding: 30px 20px;
	}
	
	.welcome-card h3 {
		font-size: 1.5rem;
	}
	
	.welcome-features {
		gap: 12px;
	}
	
	.feature-item {
		padding: 10px 12px;
	}
	
	.files-display-section {
		padding: 20px;
	}
	
	.files-grid {
		grid-template-columns: 1fr;
		max-height: 250px;
	}
	
	.file-item {
		padding: 6px 10px;
	}
	
	.memory-test-section {
		padding: 20px;
	}
	
	.memory-stats {
		grid-template-columns: 1fr;
		gap: 10px;
	}
	
	.memory-test-results {
		padding: 15px;
	}
	
	.memory-test-progress {
		padding: 15px;
	}
	
	.progress-bar-container {
		flex-direction: column;
		gap: 10px;
	}
	
	.progress-text {
		order: -1;
	}
	
	.progress-details {
		grid-template-columns: 1fr;
		gap: 8px;
	}
	
	.file-size-slider-section {
		padding: 12px;
	}
	
	.slider-container {
		flex-direction: column;
		gap: 10px;
	}
	
	.slider-value {
		order: -1;
	}
}
